import React, { Component } from 'react';
import './footer.css';
import TextField from '@material-ui/core/TextField';
import submitArrow from '../../components/images/send.png';
class Footer extends Component {
    state = {}
    render() {
        return (
            <div className="footerContainer">
                <div className="rightSection">
                    <div className="upperSection">
                        <div className="linksContainer">
                            <h3>Company</h3>
                            <ul>
                                <li>Location</li>
                                <li>About</li>
                                <li>Testimonials</li>
                                <li>Carrers</li>
                                <li>NewsRoom</li>
                                <li>FAQ's</li>
                            </ul>
                        </div>
                        <div className="linksContainer linksAdjust2">
                            <h3>Partners</h3>
                            <ul>
                                <li>Register</li>
                                <li>Login</li>
                            </ul>
                        </div>
                        <div className="linksContainer linksAdjust3">
                            <h3>Legal</h3>
                            <ul>
                                <li>Privacy</li>
                                <li>Terms</li>
                                <li>Cookies</li>
                            </ul>
                        </div>
                    </div>
                    <div className="lowerSection">
                        <div className="lowerText">Address Details</div>
                        <div className="lowerText">Contact Number</div>
                    </div>
                </div>
                <div className="middleSection">Copyright Hubshub India Pvt. Ltd.</div>
                <div className="leftSection">
                    <div className="subscribeContainer">
                        <div className="socicalMediaText">Follow us</div>
                        <div className="socialMediaContainer">
                            <div class="fab fa-facebook-f"></div>
                            <div class="fab fa-instagram"></div>
                            <div class="fab fa-twitter"></div>
                        </div>
                        <div className="subscribeHeadingText">Subscribe to our mailing list</div>
                        <div className="subscribeInputContainer">
                            <TextField
                                required
                                id="outlined-required"
                                label="Email-id"
                                variant="outlined"
                            />
                            <div className="rightArrowContainer">
                                <img src={submitArrow} alt="" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Footer;